<template lang="html">
  <transition :name="transitionName" @enter="handleEnter" @leave="handleLeave">

    <div :class="classes" :style="styles">
      <template v-if="type === 'notice'">
        <div :class="contentClasses" ref="content" v-html="content"></div>

        <div :class="contentWithIcon">
          <RenderCell :render="renderFunc"/>
        </div>

        <a :class="[baseClass + '-close']" @click="close" v-if="closable">
          <!-- <i class="ion ion-ios-close-empty"></i> -->
          <Icon type="ios-close-empty"/>
        </a>
      </template>

      <template v-if="type === 'message'">
        <div :class="[baseClass + '-content']" ref="content">
          <div :class="[baseClass + '-content-text']" v-html="content"></div>

          <div :class="[baseClass + '-content-text']">
            <RenderCell :render="renderFunc"/>
          </div>

          <a :class="[baseClass + '-close']" @click="close" v-if="closable">
            <!-- <i class="ion ion-ios-close-empty"></i> -->
            <Icon type="ios-close-empty"/>
          </a>
        </div>
      </template>
    </div>

  </transition>
</template>

<script>
export default {
  props: {
    prefixCls: {
      type: String,
      default: ''
    },

    duration: {
      type: Number,
      default: 1.5
    },

    type: {
      type: String
    },

    content: {
      type: String,
      default: ''
    },

    withIcon: Boolean,

    render: {
      type: Function
    },

    hasTitle: Boolean,

    styles: {
      type: Object,
      default: () => {
        return {
          right: '50%'
        }
      }
    },

    closable: {
      type: Boolean,
      default: false
    },

    className: {
      type: String
    },

    name: {
      type: String,
      required: true
    },

    onClose: {
      type: Function
    },

    transitionName: {
      type: String
    }
  },

  data () {
    return {
      withDesc: false
    }
  },

  mounted () {
    this.clearCloseTimer()

    if (this.duration !== 0) {
      this.closeTimer = setTimeout(() => {
        this.close()
      }, this.duration * 1000)
    }

    // check if with desc in Notice component
    if (this.prefixCls === 'ivu-notice') {
      let desc = this.$refs.content.querySelectorAll(`.${this.prefixCls}-desc`)[0]
      this.withDesc = this.render
        ? true
        : (desc ? desc.innerHTML !== '' : false)
    }
  },

  computed: {
    baseClass () {
      return `${this.prefixCls}-notice`
    },

    renderFunc () {
      return this.render || function () {}
    },

    classes () {
      return [
        this.baseClass,
        {
          [`${this.className}`]: !!this.className,
          [`${this.baseClass}-closable`]: this.closable,
          [`${this.baseClass}-with-desc`]: this.withDesc
        }
      ]
    },

    contentClasses () {
      return [
        `${this.baseClass}-content`,
        this.render !== undefined
          ? `${this.baseClass}-content-with-render`
          : ''
      ]
    },

    contentWithIcon () {
      return [
        this.withIcon
          ? `${this.prefixCls}-content-with-icon`
          : '',
        !this.hasTitle && this.withIcon
          ? `${this.prefixCls}-content-with-render-notitle`
          : ''
      ]
    },

    messageClasses () {
      return [
        `${this.baseClass}-content`,
        this.render !== undefined ? `${this.baseClass}-content-with-render` : ''
      ]
    }
  },

  methods: {
    clearCloseTimer () {
      if (this.closeTimer) {
        clearTimeout(this.closeTimer)
        this.closeTimer = null
      }
    },

    close () {
      this.clearCloseTimer()
      this.onClose()
      this.$parent.close(this.name)
    },

    handleEnter (el) {
      if (this.type === 'message') {
        el.style.height = el.scrollHeight + 'px'
      }
    },

    handleLeave (el) {
      if (this.type === 'message') {
        //  Message js 
        if (document.getElementsByClassName('ivu-message-notice').length !== 1) {
          el.style.height = 0
          el.style.paddingTop = 0
          el.style.paddingBottom = 0
        }
      }
    }
  },

  components: {
    RenderCell: {
      name: 'RenderCell',
      functional: true,
      props: {
        render: Function
      },
      render: (h, ctx) => {
        return ctx.props.render(h)
      }
    }
  },

  beforeDestroy () {
    this.clearCloseTimer()
  }
}
</script>



